.App {
  min-height: 100vh;
  min-width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.Timers {
  flex: 1 1 100%;
  display: flex;
  flex-direction: row;
  max-width: 20rem;
  align-items: center;
  flex-wrap: wrap;
  padding: 2rem;
  box-sizing: border-box;
}

.Timer {
  flex: 0 0 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 3rem;
  align-items: center;
}

.Name {
  font-size: 4rem;
}

.Time {
  font-size: 1rem;
}